/** Media Queries **/
html {
  font-size: 62.5%;
  box-sizing: border-box;
  height: 100%;
}

body {
  min-height: 100%;
  display: flex;
  flex-direction: column;
  min-width: 30rem;
}

*, *:before, *:after {
  box-sizing: inherit;
}

body {
  font-family: "Nunito Sans", sans-serif;
  font-size: 1.6rem;
}

.contenedor {
  width: 95%;
  max-width: 1200px;
  margin: 0 auto;
}

a {
  text-decoration: none;
}

img {
  max-width: 100%;
}

h1, h2, h3, h4 {
  font-family: "Nunito Sans", sans-serif;
  text-align: center;
}

h1 {
  font-size: 4rem;
}

h2 {
  font-size: 3.6rem;
}

h3 {
  font-size: 3rem;
}

h4 {
  font-size: 2.6rem;
}

p {
  font-size: 1.4rem;
}
@media (min-width: 768px) {
  p {
    font-size: 1.6rem;
  }
}

@media (min-width: 768px) {
  .pantalla-login {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    min-height: 92vh;
  }
}
.pantalla-login .imagen {
  height: 15rem;
  background-image: url("../img/login.jpg");
  background-size: cover;
  background-position: center center;
}
@media (min-width: 768px) {
  .pantalla-login .imagen {
    height: auto;
    clip-path: polygon(20% 0, 100% 0, 100% 75%, 80% 100%, 0 100%, 0 25%);
  }
}
.pantalla-login .formulario-login {
  background-color: #0da6f3;
  display: flex;
  flex-direction: column;
  justify-content: space-evenly;
  align-items: center;
  width: 70%;
  height: 90%;
  border-radius: 1rem;
  padding: 2rem 1rem;
  margin: 2rem auto;
}
@media (min-width: 768px) {
  .pantalla-login .formulario-login {
    margin: auto;
  }
}
.pantalla-login .formulario-login p:not(.condiciones) {
  text-align: center;
  font-size: 1.8rem;
}
.pantalla-login .formulario-login form {
  width: 90%;
  margin: 0 auto;
}

.options {
  width: 80%;
  display: flex;
  flex-direction: column;
  align-items: center;
}
.options a, .options input {
  background-color: #FFFFFF;
  text-align: center;
  color: #000000;
  font-weight: 700;
  padding: 1rem;
  margin: 1rem auto;
  border-radius: 1rem;
  width: 100%;
  transition: all 200ms ease;
}
.options a:hover, .options input:hover {
  background-color: #dddddd;
}
.options input {
  border: none;
  width: 90%;
}
.options input[type=submit] {
  cursor: pointer;
}

.condiciones {
  font-size: 1.4rem;
  margin: 0.5rem 2rem;
  text-align: center;
  line-height: auto;
}
.condiciones a {
  color: #FFFFFF;
}

.sibling {
  background-color: #0da6f3;
  min-height: 8rem;
  height: auto;
  padding: 2rem;
  display: flex;
  justify-content: center;
}
.sibling h1 {
  margin: auto;
}

.user-options {
  list-style: none;
  padding: 0;
  display: grid;
  grid-template-rows: repeat(3, 1fr);
  gap: 2rem;
  margin: 2rem auto;
  width: 80%;
}
@media (min-width: 768px) {
  .user-options {
    height: 72vh;
    grid-template-rows: none;
    grid-template-columns: repeat(3, 1fr);
    gap: 3rem;
  }
}

.user-option {
  border-radius: 2rem;
  height: 30rem;
  position: relative;
  transition: all 250ms ease;
}
.user-option img {
  border-radius: 2rem;
  position: absolute;
  height: 100%;
  width: 100%;
  object-fit: cover;
}
.user-option div {
  border-radius: 2rem;
  position: absolute;
  height: 100%;
  width: 100%;
  background-color: rgba(0, 0, 0, 0.6);
}
.user-option div h3 {
  text-align: start;
  color: #FFFFFF;
  margin: 1rem 0 0 1rem;
  font-size: 3.4rem;
}
.user-option:hover {
  background-color: rgba(0, 0, 0, 0.4);
  transform: scale(1.05);
}
@media (min-width: 768px) {
  .user-option {
    height: auto;
  }
  .user-option div h3 {
    transition: all 150ms ease;
    font-size: 2.4rem;
  }
}
@media (min-width: 1024px) {
  .user-option {
    height: auto;
  }
  .user-option div h3 {
    transition: all 150ms ease;
    font-size: 3.4rem;
  }
}

.cities {
  margin: 2rem auto;
}
.cities .city .city-list {
  list-style: none;
  height: auto;
  padding: 0.5rem;
}
.cities .city .city-list a {
  border: 1px solid black;
  width: 100%;
  display: block;
  padding: 1rem;
  text-align: center;
  border: 1px solid #000000;
  font-weight: 700;
  color: #000000;
  transition: all 250ms ease;
}
.cities .city .city-list a:hover {
  background-color: #0da6f3;
  color: #FFFFFF;
}
.cities .city .city-list a:first-of-type {
  border-radius: 1rem 1rem 0 0;
}
.cities .city .city-list a:last-of-type {
  border-radius: 0 0 1rem 1rem;
}
@media (min-width: 768px) {
  .cities {
    display: grid;
    grid-template-columns: repeat(3, 1fr);
    gap: 2rem;
  }
}

.city-title {
  color: #0da6f3;
}
.city-title::after {
  content: "";
  display: block;
  width: 50%;
  margin: auto;
  height: 0.4rem;
  background-color: #0da6f3;
  border-radius: 0.5rem;
}

.dealerships {
  margin: auto;
}
.dealerships a {
  width: 80%;
  margin: 2rem auto;
  display: block;
  text-align: center;
  border: 1px solid #dddddd;
  padding: 3rem;
  font-size: 2rem;
  font-weight: 700;
  border-radius: 1rem;
  color: #000000;
  transition: all 300ms ease;
}
.dealerships a:hover {
  background-color: #0da6f3;
  color: #FFFFFF;
  transform: scale(1.1);
}
@media (min-width: 768px) {
  .dealerships {
    display: grid;
    grid-template-columns: repeat(3, 1fr);
    gap: 2rem 4rem;
  }
  .dealerships a {
    width: 100%;
  }
}

.ajuste-alto {
  min-height: 80vh;
  margin: 2rem auto;
}

.cars .car {
  border: 1px solid #dddddd;
  margin: 3rem auto;
  width: 80%;
  border-radius: 0 1rem 1rem 0;
  transition: all 250ms ease;
}
.cars .car .info-car {
  padding: 1rem;
}
.cars .car .info-car h4 {
  margin: 0;
  font-size: 1.8rem;
}
.cars .car .info-car p {
  color: #0da6f3;
  font-weight: 700;
}
.cars .car .info-car span {
  color: #000000;
}
.cars .car:hover {
  transform: scale(1.05);
}
@media (min-width: 768px) {
  .cars .car {
    width: 90%;
    display: grid;
    grid-template-columns: 30% 70%;
  }
}

.sombra {
  -webkit-box-shadow: 0px 0px 17px 10px rgba(0, 0, 0, 0.16);
  -moz-box-shadow: 0px 0px 17px 10px rgba(0, 0, 0, 0.16);
  box-shadow: 0px 0px 17px 10px rgba(0, 0, 0, 0.16);
}

.header {
  background-color: #000000;
}
.header .contenedor .barra {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}
.header .contenedor .barra .navegacion a {
  display: block;
  text-align: center;
  background-color: #FFFFFF;
  padding: 1rem;
  margin: 1rem auto;
  color: #0da6f3;
  border-radius: 1rem;
  font-weight: 700;
  transition: all 250ms ease;
}
.header .contenedor .barra .navegacion a:hover {
  background-color: #0da6f3;
  color: #FFFFFF;
}
@media (min-width: 768px) {
  .header .contenedor {
    height: 6rem;
  }
  .header .contenedor .barra {
    height: 100%;
    flex-direction: row;
    justify-content: space-between;
  }
  .header .contenedor .barra a {
    height: 80%;
  }
  .header .contenedor .barra a img {
    height: 100%;
  }
  .header .contenedor .barra .navegacion {
    display: flex;
  }
  .header .contenedor .barra .navegacion a {
    margin-left: 2rem;
  }
  .header .contenedor .barra .navegacion a:first-of-type {
    margin-left: 0;
  }
}

/*menu de hamburguesa*/
.mobile-menu {
  cursor: pointer;
}
@media (min-width: 768px) {
  .mobile-menu {
    display: none;
  }
}
.mobile-menu img {
  width: 5rem;
}

.mostrar {
  display: block;
}

.footer {
  background-color: #000000;
  height: 4rem;
  display: flex;
  justify-content: center;
  align-items: center;
}
.footer p {
  color: #FFFFFF;
}

.buscador {
  background-color: #0da6f3;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}
.buscador input {
  border-radius: 0.5rem;
  border: none;
  margin: 2rem auto;
  padding: 0.5rem 1rem;
  display: block;
}
.buscador input[type=submit] {
  color: #0da6f3;
  background-color: #FFFFFF;
  transition: all 250ms ease;
}
.buscador input[type=submit]:hover {
  cursor: pointer;
  font-weight: 700;
  transform: scale(1.1);
}
@media (min-width: 768px) {
  .buscador {
    flex-direction: row;
  }
  .buscador input {
    display: inline;
    margin-right: 1rem;
  }
  .buscador input:last-of-type {
    margin-right: 0;
  }
}